.App {
  text-align: center;
}

.mainPage {
  display: flex;
  flex: 1;
}

.homeGrid {
  display: flex;
  flex: 1;
  width: 100%;
  background-color: rgb(10, 66, 29);
}
.navHeader {
  background-color: rgb(10, 66, 29);
}
.navList {
  width: 100%;
  display: flex;
  justify-content: flex-start;
}

.wallPaintings {
  flex-direction: column;
  display: flex;
  width: 100%;
}

.input {
  display: flex;
  flex-direction: column;
  width: 50%;
}

.inputBox {
  width: 100%;
}
.notalink {
  background: none !important;
  border: none;
  padding: 0 !important;
  font-size: 64px;
  /*optional*/
  font-family: arial, sans-serif;
  /*input has OS specific font-family*/
  color: #069;
  text-decoration: underline;
  cursor: pointer;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #0d5324;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
